import React, { useEffect, useState } from 'react';
import './App.scss';
import "aos/dist/aos.css";
import name from './assets/img/name.svg';
import photo from './assets/img/photo.webp';
import video from './assets/img/video.jpg';
import menu from './assets/img/burgermenu.svg';

function Main() {
  
  const [scrollY, setScrollY] = useState(0);

  const [PaddingTop, setPaddingTop] = useState(196);

  const [PosLeft, setPosLeft] = useState(-100);
  
  function logit() {
      setScrollY(window.pageYOffset);
      if (scrollY <=360){
        setPaddingTop(196+scrollY);
        setPosLeft(-100+scrollY*0.295 );
      }
      
    }
  
    useEffect(() => {
      function watchScroll() {
        window.addEventListener("scroll", logit);
      }
      watchScroll();
      // Remove listener (like componentWillUnmount)
      return () => {
        window.removeEventListener("scroll", logit);
      };
    });

  return (
        <div className="MainPage">
        <div className="mainimgbg image">
          <div style={{paddingTop: `${PaddingTop}px`}} className="name image">
            <img src={name}/>
          </div>
          <div className="photo image">
            <img src={photo}/>
          </div>
          <div style={{left : `${PosLeft}%`}} className="description">
            PERSONAL <br/>
            SHOPPER
          </div>
          <div className="fixed-center">Scroll position: {scrollY}px</div>
        </div>
        <div className="video image">
          <img src={video}/>
        </div>
      </div>
  );
}

export default Main;